      margin-bottom 20px
      text-align center
    .choosed
      font-size 14px
      color #444444
      text-align right
      height 30px
      line-height 30px
      b
        color #FD9D28
        padding-right 20px
        margin-right 20px
        border-right 2px solid #cccccc
      i
        display inline-block
        width 20px
        height 20px
        vertical-align middle
    h2
      font-size 16px
      line-height 1
      padding-top 16px
      padding-bottom 16px
      font-weight bold
      border-bottom 1px solid #dedede
    h4
      margin-top 20px
      margin-bottom 20px
      color #444444
    .step
      padding 20px
      font-size 14px
      color #3c3c3c
      .input
        margin-bottom 32px
        .readShopInfo
          border 1px solid #FF3341
          margin-left 12px
          display inline-block
          height 36px
          line-height 36px
          text-align center
          cursor pointer
          width 160px
          font-size 12px
          color #FF3341
        p
          font-size 12px
          color #9b9b9b
          margin-top 8px
          margin-left 77px
    .uploadImg
      margin-top 12px
      margin-bottom 32px
      width 120px
      height 150px
      border 1px solid #dedede
      text-align center
      .avatar
        width 120px
        height 120px
        display block
      .avatar-uploader-icon
        font-size 50px
        color #8c939d
        width 120px
        height 120px
        line-height 120px
        text-align center
      h3
        font-size 14px
        line-height 30px
        height 30px
        color #9b9b9b
        background #efefef
    .setFormat
      margin-top 32px
      margin-bottom 12px
    .table
      width 100%
      tr
        height 40px
        line-height 40px
        text-align center
      th
        border 1px solid #dedede
        background #fafafa
      td
        border 1px solid #dedede
        input
          width 100%
          outline none
          text-align center
          &:disabled
            color #dedede
            background none
            &:hover
              cursor not-allowed
    .checkBox
      height 14px
      line-height 1
      margin-top 20px
      margin-bottom 16px
    .payWay
      li
        font-size 14px
        color #444444
        height 14px
        margin-bottom 26px
