  .step4
    .server
      >li
        margin 27px 0
        font-size 14px
      .area
        border 1px solid #DEDEDE
        li
          height 40px
          line-height 40px
          padding 0 20px
          &:nth-child(2n)
            background rgba(23, 159, 255, 0.1)
          .bold
            margin-right 50px
</style>


